@font-face {
  font-family: CONDENSEicon;
  font-display: swap;
  src: url(assets/fonts/CONDENSEicon.woff2) format("woff2"), url(assets/fonts/CONDENSEicon.woff) format("woff"), url(assets/fonts/CONDENSEicon.ttf) format("truetype"); }

@font-face {
  font-family: Quicksand;
  font-display: swap;
  src: url(assets/fonts/Quicksand.woff2) format("woff2"), url(assets/fonts/Quicksand.woff) format("woff"), url(assets/fonts/Quicksand.ttf) format("truetype"); }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeOut {
  to {
    opacity: 0; } }

.fadeIn {
  animation: .3s fadeIn forwards; }

.fadeOut {
  animation: .3s fadeOut forwards; }

img {
  vertical-align: bottom; }

h1, h2, p {
  padding: 0;
  margin: 0; }

a {
  outline: 0;
  text-decoration: none;
  color: #3C3C3C; }

body {
  padding: 0;
  margin: 0;
  color: #3C3C3C;
  font-size: 12px;
  font-family: SmartFontUI, sans-serif;
  line-height: 14px;
  background: url(assets/background.jpg), #F5F5F5;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; }

main {
  top: 50%;
  left: 50%;
  position: absolute;
  margin-top: -300px;
  margin-left: -400px;
  opacity: 0;
  animation: .5s fadeIn forwards; }

#container {
  width: 704px;
  height: 504px;
  padding: 47px;
  background: radial-gradient(circle at top left, #FFFFFF, #FAFAFA);
  border: 1px solid #E6E6E6;
  border-radius: 4px;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.12); }
  #container .relative {
    width: 704px;
    height: 504px; }

.relative {
  position: relative; }

.absolute {
  width: 100%;
  position: absolute; }

header h1 {
  height: 26px;
  font-size: 24px;
  line-height: 26px;
  font-family: Quicksand;
  margin-top: 24px;
  margin-left: 24px; }

header #profile-icon {
  width: 128px;
  height: 128px;
  border: 4px solid #E6E6E6;
  border-radius: 100%;
  background: url(assets/icon.jpg) center/cover no-repeat;
  top: 0;
  right: 48px;
  position: absolute; }

header #border {
  display: flex;
  justify-content: space-between;
  margin: 16px 0; }
  header #border div {
    background: #E6E6E6;
    width: 100%;
    height: 2px; }
    header #border div:first-child {
      width: 504px; }
    header #border div:last-child {
      width: 32px; }

header p {
  margin-left: 24px;
  margin-right: 24px;
  margin-bottom: 8px;
  line-height: 12px;
  height: 12px;
  white-space: pre-wrap;
  font-family: Quicksand; }
  header p span {
    margin-right: 8px;
    font-family: CONDENSEicon; }

#body {
  width: 100%;
  top: 184px;
  position: absolute;
  display: flex;
  justify-content: space-between; }

#articles {
  display: flex; }
  #articles article {
    width: 200px;
    height: 228px;
    border: 1px solid #E6E6E6;
    border-radius: 4px;
    padding: 15px;
    position: relative;
    background: #FFFFFF; }
    #articles article:first-child {
      margin-right: 48px; }
    #articles article h2 {
      font-size: 12px;
      line-height: 12px;
      font-family: Quicksand;
      margin-bottom: 16px; }
    #articles article .article {
      width: 166px;
      height: 38px;
      border: 1px solid #E6E6E6;
      border-radius: 4px;
      margin-bottom: 16px;
      padding: 16px;
      display: flex;
      flex-direction: column;
      justify-content: space-between; }
      #articles article .article:hover {
        transition: 0.25s ease;
        background: #E6E6E6; }
      #articles article .article .title {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis; }
      #articles article .article .footer {
        color: #787878;
        font-family: Quicksand;
        font-size: 10px;
        line-height: 10px;
        display: flex;
        justify-content: space-between; }
    #articles article .read-more {
      width: 198px;
      height: 22px;
      border: 1px solid #E6E6E6;
      border-radius: 4px;
      font-family: Quicksand;
      font-size: 10px;
      line-height: 24px;
      text-align: center;
      bottom: 15px;
      position: absolute; }
      #articles article .read-more:hover {
        transition: 0.25s ease;
        background: #E6E6E6; }

nav .link {
  width: 110px;
  height: 30px;
  border: 1px solid #E6E6E6;
  border-radius: 4px;
  margin-bottom: 16px;
  padding: 0 16px;
  line-height: 30px;
  font-family: Quicksand;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  background: #FFFFFF; }
  nav .link:hover {
    transition: 0.25s ease;
    background: #E6E6E6; }
  nav .link div:first-child img {
    vertical-align: middle; }

footer #copyright {
  bottom: 0;
  right: 0;
  position: absolute;
  font-family: Quicksand;
  font-size: 10px;
  line-height: 10px; }

.resume {
  text-decoration: underline; }
